import styled from "styled-components";
import { Form as Unform } from '@unform/web';

export const Container = styled.div`
    width: 450px;
    height: 650px;
    margin: 0 auto;
    padding: 12px;
    
    margin-top: 2.5rem;

    background: #80aaff;
    border-radius: 10px;
    border: 2px solid black;
    box-shadow: -5px 10px 20px black;
    margin-bottom: 40px;

    @media screen and (max-height: 750px){
        height: 570px;
        margin-bottom: 5px;
    }

`;

export const Form = styled(Unform)`

    position: relative;

        height: 100%;

    label{
        font-size: 14px;
        font-weight: bold;
        margin-left: 20px;
        margin-top: 33px;
        display: block;

        &:first-child{
            margin-top: 10px;
        }
    }

    span{
        position: absolute;
        margin-left: 22px;
        text-align: right;
        font-size: 12px;
        margin-top: 2px;
        font-weight: bold;

    }



    input{
        margin: 0 auto;
        width: 90%;
        display: block;
        background-color: #e6e6e6;
        margin-top: 4px;
        height: 1.8rem;
        border-radius: 5px;
        border: none;
        text-align: center;

            &[type="number"]{
                margin-left: 20px;
                width: 100px;

            }
        }

        button{
            width: 50%;
            height: 2.5rem;
            font-weight: bolder;
            border-radius: 10px;
            border: none;
            background-color: #b3b3b3;
            position: absolute;
            bottom: 15px;
            right: 0; left: 0;
            margin: 0 auto;
            transition: filter 0.3s;

            cursor: pointer;

            &:hover{
                filter: brightness(0.9);
                border: solid 2px black;
            }

        }

        @media screen and (max-height: 750px){
     
            label{
                font-size: 13px;
                margin-top: 22px;
                display: block;

            &:first-child{
                margin-top: 5px;
            }
        }

        span{
            font-size: 11px;
            margin-top: 1px;
            font-weight: bold;

        }

        input{
            margin-top: 5px;
        }
            button{
            height: 2rem;
        }
          }

`;


export const Success = styled.div`

          background-color: #FFF;

`;