import React, { useRef, useState, useEffect } from 'react';
import { Scope } from '@unform/core';
import { Input } from '../../Form/input';
import * as Yup from 'yup';
import { Container, Form, Success } from './styles';


export function Main(){

    const formRef = useRef(null);

    async function handleSubmit(data, { reset }){
        
        // Validações
        try {
            const schema = Yup.object().shape({
                name: Yup.string()
                    .min(2, 'Digite ao menos 2 caracteres')
                    .required('Informe seu nome'),
                password: Yup.string()
                    .min(5, 'Digite ao menos 5 caracteres'),
                email: Yup.string()
                    .email('Informe um email válido')
                    .required('Informe seu email'),
                address: Yup.object().shape({
                    street: Yup.string()
                        .min(4, 'Digite ao menos 4 caracteres')
                        .required('Informe sua cidade'),
                    city: Yup.string()
                        .min(3, 'Digite ao menos 3 caracteres')
                        .required('Informe sua cidade'),
                    number: Yup.string()
                        .required('Informe o número')
                    
                })
            })

        await schema.validate(data, {
            abortEarly: false,
        }) 

        console.log(data);
        reset();
        formRef.current.setErrors({});

    } catch (err) {
        if (err instanceof Yup.ValidationError) {
            const errorMessages = {};

            err.inner.forEach(error => {
                errorMessages[error.path] = error.message;
            })

            formRef.current.setErrors(errorMessages);
            }
        }
    }


    // Caso queira já começar com algum dado no form
    // useEffect(() => {
    //     setTimeout(() => {
    //         formRef.current.setData({
    //             name: 'Gabriel Mançano',
    //             email: 'gabriel@yahoo.com'
    //         })
    //     }, 2000);
    // })

    return(
        <Container>
            <Form ref={ formRef } onSubmit={ handleSubmit }>
                <label>Nome:</label>
                <Input name="name" />

                <label>Email:</label>
                <Input name="email" />

                <label>Senha:</label>
                <Input type="password" name="password" />

                <Scope path="address">
                    <label>Rua:</label>
                    <Input name="street"  />
                    <label>nº:</label>
                    <Input type="number" name="number" />

                    <label>Cidade:</label>
                    <Input name="city" />
                    
                </Scope>

                <button type='submit' >Cadastrar</button>
            </Form>
        </Container>
        
    )
}