import styled from "styled-components";

export const Container = styled.footer`
    color: 'black';
    opacity: 0.6;
    position: fixed;
    bottom: 0;
    left: 0; right: 0;
    width: 400px;
    margin: auto;
    text-align:center;
    font-weight: 400;
    font-size: 17px;

    &:hover{
        opacity: 1;
        cursor: grab;
        font-weight: bolder;
    }


    @media screen and (max-height: 820px){
        display: none;
    }
`